/* reset */
* {
    margin: 0;
    padding: 0;
    text-decoration: none;
}

@media only screen and (min-device-width: 375px) and (-webkit-min-device-pixel-ratio: 1) {
    .sidebar {
        display: none;


    }

    .search {
        background: #5F2EEA;
        height: 15px;
        width: 15px;
        padding-top: 3px;
        padding-right: 3px;
        padding-left: 3px;
        padding-bottom: 3px;
        border-radius: 10px;
        margin-right: 10px;
        margin-top: 30px;
    }

    .watch {
        margin-right: 15px;
        margin-top: 15px;
    }


    .sidebar h2 {
        text-align: center;
        font-size: 20px;
        text-transform: uppercase;
        color: #14142B;
        background: white;
        padding: 20px;
        font-weight: bold;
    }

    .sidebar ul {
        padding-top: 20px;
    }

    .sidebar ul li {
        margin: 30px 0;
    }

    .sidebar ul li a {
        color: #000000;
        padding: 0 40px;
        font-family: 'Poppins', sans-serif;
        font-weight: bold;
        font-size: 0.7rem;


    }

    .sidebar ul li a:hover {
        color: #000000;
        margin-left: 20px;
        transition: 0.4s;
    }

    .sidebar ul li a:active {
        border-left: 3px solid #5F2EEA;
        margin-left: 10px;

    }
}


