@media only screen and (min-device-width: 375px) and (-webkit-min-device-pixel-ratio: 1) {

    .main {


        padding: 5px;
    }


    .info {
        position: relative;

        width: 100%;



    }

    .title {

        font-family: 'Poppins', sans-serif;
        padding-bottom: 20px;
        font-size: 2rem;


    }


    p {

        font-size: 1.5rem;
        padding-left: 20px;
        font-weight: 500;
        width: 70%;
        text-align: center;
        font-family: "Times New Roman", Times, serif;

    }

    .un {
        display: flex;
        position: relative;
        flex-wrap: wrap;
        padding-top: 40px;
        width: 60%;
        grid-gap: 5px;


        grid-template-columns: repeat(auto-fit);
        list-style: none;
    }

    .list {
        padding-left: 30px;
        color: #A0A3BD;
    }

    .butt {

        background-color: #5F2EEA;
        color: white;

        font-family: 'Poppins', sans-serif;
        font-weight: bolder;
        font-size: 0.7rem;
        width: 200px;
        height: 50px;
        margin-top: 30px;

        text-align: center;
        border-radius: 30px;
    }

}

@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) and (-webkit-min-device-pixel-ratio: 1) {

